@import url('https://fonts.googleapis.com/css?family=Roboto:400,700,900&display=swap');
@import url('https://fonts.googleapis.com/css?family=Lato:400,600,700,900&display=swap');
@import './../../variables.scss';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    transition: all 0.4s;
}

.sci-name-style {
    font-style: italic;
}

a {

    &:hover,
    &:link,
    &:visited,
    &:active {
        text-decoration: none;
        color: #333333;
    }
}

body {
    font-family: 'Lato', sans-serif;
}

@keyframes showcomponent {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
}

.firstdisplay {
    animation: showcomponent 1.3s 1;
}

/* width */
::-webkit-scrollbar {
    width: 8px;
    height: 8px;
    ;
}

/* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: $mainActiveColor;
}

.green-text {
