}

.bold {
    font-weight: bold;
}

h1 {
    font-size: 18px;
}
.undisplay {
    display: none;
}

.tooltip {
    position: relative;

    &:hover .tooltiptext {
        display: inherit;
    }

    &:hover {

        .tooltiptextUp,
        .tooltiptext {
            display: inherit;
        }
    }


    .tooltiptext {
        display: none;
        min-width: 120px;
        word-wrap: break-word;
        max-width: 210px;
        background-color: $mainDarkColor;
        color: $white;
        text-align: center;
        border-radius: 6px;
        padding: 5px 0;
        position: absolute;
        z-index: 1;
        left: 50%;
        margin-left: -60px;
        top: 120%;



        &::after {
            content: "";
            position: absolute;
            bottom: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: transparent transparent $mainDarkColor transparent;
        }
    }

    .tooltiptextUp {
        display: none;
        min-width: 120px;
        word-wrap: break-word;
        max-width: 210px;
        background-color: $mainDarkColor;
        color: $white;
        text-align: center;
        border-radius: 6px;
        padding: 5px;
        position: absolute;
        z-index: 1;
        left: 50%;
        margin-left: -60px;
        bottom: 150%;

        &::after {
            content: "";
            position: absolute;
            top: 100%;
            left: 50%;
            margin-left: -5px;
            border-width: 5px;
            border-style: solid;
            border-color: $mainDarkColor transparent transparent transparent;
        }
    }
}

.close-icon {
