.check-container {
    position: relative;
    padding-left: 25px;
    margin-bottom: 12px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    &:hover input~.checkmark {
        background-color: rgb(247, 247, 247);
    }

    input {
        position: absolute;
        opacity: 0;
        cursor: pointer;
        height: 0;
        width: 0;

        &:checked~.checkmark:after {
            display: block;
        }

        &:checked~.checkmark {
            background-color: $secondActiveColor;
            border: none;

        }
    }

    .checkmark {
        position: absolute;
        top: 0;
        left: 0;
        height: 20px;
        width: 20px;
        background-color: rgb(255, 255, 255);
        border-radius: 4px;
        border: 1.5px solid $mainActiveColor;

        &:after {
            content: "";
            position: absolute;
            display: none;
        }

        &:after {
            left: 6px;
            top: 2px;
            width: 5px;
            height: 10px;
            border: solid white;
            border-width: 0 3px 3px 0;
            -webkit-transform: rotate(45deg);
            -ms-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }

}

.arrow-down {
    width: 0;
    height: 0;
    border-top: 7px solid transparent;
    border-bottom: 7px solid transparent;
    border-right: 7px solid $mainActiveColor;
    transform: rotate(-90deg);
    border-radius: 15px;
    margin: 0 0.5rem 0 0;
    &.white {
        border-right: 7px solid white;
    }
    &:hover {
        border-right: 7px solid $mainDarkColor;
        cursor: pointer;
    }
}

.arrow-left {
    width: 0;
    height: 0;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    border-right: 10px solid $mainActiveColor;
    transform: rotate(0deg);
    border-radius: 15px;
    &.white {
        border-right: 10px solid white;
    }
    &:hover {
        border-right: 10px solid $mainDarkColor;
        cursor: pointer;
    }
}