@import "./src/variables.scss";

.marks {
    display: flex;
    position: relative;
    height: 55px;
    width: 100%;
    background-color: #FFF;
    flex-direction: row;

    .mark {
        position: absolute;
        bottom: 0;
        //left: 0;
        cursor: pointer;
        display: flex;
        flex-direction: column-reverse;
        align-content: center;
        align-items: center;

        &.prevent-hover {
            pointer-events: none;
        }

        &:hover {
            z-index: 100;

            .bird-photo-cont {
                transform: scale(1.2);
                margin: 0 0 0.3rem 0;

                img {
                    opacity: 1;
                }
            }
        }

        &.selected {
            z-index: 100;

            .bird-photo-cont {
                transform: scale(1.3);
                margin: 0 0 0.5rem 0;
                border: solid 1.5px $thirdActiveColor;

                img {
                    opacity: 1;
                }
            }

            .mark-info {
                display: flex;
                z-index: 100;

            }
        }

        &.selected-sel{
            z-index: 100;

            .bird-photo-cont {
                transform: scale(1.3);
                margin: 0 0 0.5rem 0;
                border: solid 1.5px $thirdActiveColor;

                img {
                    opacity: 1;
                }
            }
        }

        &:first-child {
            bottom: 0;
            //left: 427px;
        }

        &:nth-child(2) {
            bottom: 0;
            //left: 456px;
        }

        &:nth-child(3) {
            bottom: 0;
            //left: 503px;
        }

        &:nth-child(4) {
            bottom: 0;
            //left: 681px;
        }

        &:nth-child(5) {
            bottom: 0;
            //left: 809px;
        }

        &:nth-child(6) {
            bottom: 0;
            //left: 825px;
        }

        .mark-icon {}

        .bird-photo-cont {
            width: 30px;
            height: 30px;
            border-radius: 15px;
            margin: 0 0 0.2rem 0;
            overflow: hidden;
            background-color: white;
            border: solid 1.5px transparent;

            img {
                height: 110%;
                width: 110%;
                opacity: 0.6;
                object-fit: cover;
            }
        }

        .mark-info {
            display: none;
            position: absolute;
            bottom: 0.2rem;
            left: 130%;
            width: 200px;
            background-color: $white;
            border-radius: 5px;
            pointer-events: none;
            flex-direction: row;
            padding: 0.5rem 0.5rem 0rem 0.7rem;
            box-shadow: 3px 3px 3px $boxShadow;
            border-top: 3px solid $thirdActiveColor;

            .info {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: flex-start;
                width: 90%;

                img {
                    pointer-events: all;

                }
            }

            .actions {
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: center;
                width: 20%;
                padding: 0.2rem 0;

                img {
                    
                    pointer-events: all;
                    margin: 0 0 0.2rem 0;
                    &:last-child {
                        margin: 0
                    }
                }
            }

            p {
                height: auto;
                pointer-events: user-select;
                margin: 0.1rem 0;
                -o-user-select: text;
                -moz-user-select: text;
                -webkit-user-select: text;
                user-select: text;
            }
        }
    }

}

