@import "./../../../../variables.scss";

.tagging-section {
    background-color: white;
    width: 60%;
    font-size: 15px;
    padding: 0 0 2rem 0;


    .card-section {

        .btn-actions {
            display: flex;
            flex-direction: row;
            justify-content: center;
        }
    }

    .suggest-header {
        background-color: $thirdActiveColor;
        padding: 7px 10px;

        h1 {
            font-size: 15px;
        }

        img {
            cursor: pointer;
        }
    }

    .cards-cont {
        background-color: $semiWhite;
        padding: 10px;
        position: relative;
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        .suggest-card-cont {
            display: block;
            margin: 0 .5rem 0 0;
        }


        .zoomed-spectro {
            &.undisplay {
                display: none;
            }

            position: absolute;
            top: 0;
            left: 0;
            background-color: rgba(3, 3, 3, 0.158);
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;

            .spectro-card {
                padding: 15px;
                width: 80%;
                margin: 2rem auto 0 auto;
                background-color: white;
                border-radius: 5px;
                position: relative;

                .spec-img {
                    width: 90%;
                }

                .close-img {
                    position: absolute;
                    top: 10px;
                    right: 10px;
                    height: 15px;
                    cursor: pointer;
                }

                .play {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    cursor: pointer;

                    p {
                        font-weight: 700;
                        margin: 0 0 0 0.5rem;
                    }
                }
            }

        }

        .next-card {
            position: absolute;
            top: 40%;
            right: 1.5%;
            display: flex;
            width: 30px;
            height: 30px;
            background-color: rgb(255, 254, 241);
            border-radius: 50px;
            box-shadow: 2px 2px 2px $boxShadow;
            cursor: pointer;

            img {
                margin: auto;
            }

            &:hover {
                background-color: rgb(255, 252, 225);

                transition: scale 0s;
                transform: scale(1.02);
            }
        }

        .back-card {
            position: absolute;
            top: 40%;
            left: 1.5%;
            display: flex;
            width: 30px;
            height: 30px;
            background-color: rgb(255, 254, 241);
            border-radius: 50px;
            box-shadow: 2px 2px 2px $boxShadow;
            cursor: pointer;

            img {
                margin: auto;
                transform: rotate(180deg);
            }

            &:hover {
                background-color: rgb(255, 252, 225);
                transition: scale 0s;
                transform: scale(1.02);
            }
        }

        .suggestion-actions {
            display: flex;
            flex-direction: column;
            width: 100%;
            margin: 0.5rem 0 0 0;

            .play {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;
                align-items: center;
                cursor: pointer;
                margin: 0 0 0.5rem 0;

                p {
                    font-weight: 700;
                    margin: 0 0 0 0.5rem;
                }
            }

            .match-perc {
                border-left: 3px solid $mainActiveColor;
                padding: 0 10px;
                margin: 0;
            }

            p {
                font-size: 14px;
            }

            .actions-wrapper {
                display: flex;
                flex-direction: row;

                .action-btn {
                    margin: 0.2rem 0.5rem;
                    cursor: pointer;

                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }
    }

    .suggest-card-cont {
        background-color: white;
        border-radius: 5px;
        width: 97%;
        margin: auto;
    }


    .ai-section {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        padding: 15px;

        .column-wrapper {
            width: 60%;
            height: 220px;
            display: flex;
            flex-direction: column;

            .main-spec {
                margin: 0 0.5rem 0 0;
                width: 100%;
                height: 220px;
                position: relative;
                overflow: hidden;
                border: 1.5px solid $mainActiveColor;

                .spec-img {
                    height: 100%;
                    padding: 2px;
                }

                .lens-img {
                    position: absolute;
                    top: 40%;
                    left: 40%;
                    height: 35px;
                    display: none;
                    cursor: pointer;

                    &:hover {
                        transform: scale(1.2);
                    }
                }

                &:hover {
                    .lens-img {
                        display: inherit;
                    }
                }
            }

            .coincidence-header {
                display: flex;
                justify-content: space-around;
                width: 100%;
                background-color: #53D470;

                h2 {
                    display: flex;
                    font-size: 17px;
                    color: white;
                    text-align: center;
                    margin-top: 2px;
                    margin-bottom: 2px;
                }
            }
        }


        .suggestion-info {
            width: 50%;
            padding: 0 1.5rem;
        }

        .row {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;

        }


        .bird-info {

            p {
                margin: 0.2rem 0;
                font-size: 15px;
            }
        }

        .bird-photo-cont {
            width: 70px;
            height: 70px;
            border-radius: 35px;
            margin: 0 1rem 0 0;
            overflow: hidden;
            background-color: white;
            border: solid 2px $thirdActiveColor;

            img {
                height: 115%;
                width: 115%;
                object-fit: cover;
            }
        }
    }

    .ai-sources {
        padding: 0 15px 1rem 15px;

        >p {
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            align-items: center;
            margin: 0 0 0.5rem 0;
            color: $mainDarkColor;
            font-weight: 700;
            cursor: pointer;

            &:hover {
                color: $mainActiveColor;
            }

            &.unfolded {
                div {
                    transform: rotate(90deg);
                }
            }

            div {
                margin: 0 0.5rem 0 0;
            }
        }

        .row {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            flex-wrap: wrap;

            article {
                padding: 0.2rem 0.5rem;
                margin: 0.5rem 0;
                border-left: 1px solid $thirdActiveColor;
                width: auto;
                position: relative;

                .lens-img {
                    position: absolute;
                    top: 25px;
                    left: 37%;
                    height: 35px;
                    display: none;
                    cursor: pointer;

                    &:hover {
                        transform: scale(1.2);
                    }
                }

                &:hover {
                    .lens-img {
                        display: inherit;
                    }
                }

                .spectro-img {
                    height: 80px;
                    margin: 0.2rem 0;

                }

                .play {
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    cursor: pointer;

                    p {
                        font-weight: 700;
                        margin: 0 0 0 0.5rem;
                    }
                }
            }
        }

    }
}

.spec {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: nowrap;
    align-items: flex-start;
    width: 100%;
    cursor: pointer;
    .spectro-img {
        height: 100%;
        width: auto;
    }

    .spectro-play {
        width: 100%;
        height: 35px;
        cursor: pointer;
    }
}

.card-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #AFEA7D;
    padding: 0.7rem 1rem;

    h1 {
        margin: 0;
    }

    h3 {
        font-size: 18px;
        color: #333333;
    }


}

.imgs-array {
    display: flex;
    flex-direction: row;
    margin: 0.3rem 0;

    .bird-photo {
        height: 50px;
        width: 50px;
        overflow: hidden;
        position: relative;
        border-radius: 35px;
        margin: 0 0.5rem 0 0;
        cursor: pointer;

        img {
            position: absolute;
            right: -10%;
            height: 100%;
        }
    }
}

.description-items-row {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0rem 1rem;
}

.input-row {
    &.search {
        justify-content: space-between;

        input {
            width: 50%;
        }
    }
}

.card-item {
    padding: 0.3rem 1rem;
}

.form-section {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;

    #note {
        &.undisplay {
            display: none;

        }

    }

    textarea {
        width: 100%;
        margin: 0.5rem 0;
        font-family: 'Lato', sans-serif;
    }

    .name-specie {
        .react-selector {
            width: 60%;
            margin: 0 0 0 0.5rem;
        }
    }

    .naming {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;

        span {
            width: 50%;
        }

        .react-selector {
            width: 60%;
            margin: 0 0 0 0.5rem;
        }
    }

    .actions {
        width: 40%;
        display: flex;
        flex-direction: column;
        align-content: flex-end;
        align-items: flex-end;
        justify-content: flex-start;

        p {
            text-align: right;
        }

        .react-selector {
            width: 70%;
            margin: 0 0.5rem 0 0;
        }
    }
}

.actions {
    padding: 1rem;

    p {
        margin: 0.5rem;
    }
}